
import commonTab from '@/components/commonTab'
import courseCard from '@/components/courseCard'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: 'purchaseCourse',
data(){
return{
TabList1: [
{name: '',value:'0'},
{name: '',value:'1'},
{name: '',value:'2'},
{name: '',value:'3'}
],
TabList2: [
{name: '',value:'0'},
{name: '',value:'1'}
],
trueStatus:true,
onFetching:false,
listStatus:false,
purchasetab2:false,
list:[1,2,3,4,5]
}
},
components:{
commonTab,courseCard,Scroller, LoadMore
},
methods:{
getTabIndex1(type){
console.log(type)

if(type == '0'){
this.purchasetab2 = false;
}else{
this.purchasetab2 = true;
}
},
getTabIndex2(type){
console.log(type)
},
onPullupLoading(){






},
onScrollBottom () {
if (this.onFetching) {

console.log('1111111')
} else {
this.onFetching = true
console.log('222222')
setTimeout(() => {
let lastValue = this.list[this.list.length - 1];
if (lastValue < 100) {
for (let i = 1; i <= 10; i++) {
this.list.push(lastValue + i);
}
}
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
})
this.onFetching = false
}, 10000)
}
},
},
created(){

}
}



